@font-face {
  font-family: 'Glypha-LT';
  src: url("../../../src/styles/fonts/Glypha-LT-Std-55-Roman.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

img {
  max-width: 100%;
  height: auto; }

.akiEfacil {
  padding-top: 50px;
  padding-bottom: 50px; }
  .akiEfacil .wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  .akiEfacil .text {
    position: absolute;
    color: #ffff; }
    .akiEfacil .text span {
      font-family: 'Glypha-LT';
      font-size: 65px;
      font-weight: bold; }
  .akiEfacil .outer_images {
    display: flex;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    justify-content: center; }
    @media screen and (max-width: 767px) {
      .akiEfacil .outer_images div:last-child {
        display: none; } }
    .akiEfacil .outer_images .img_div {
      object-fit: cover; }
      @media screen and (min-width: 768px) {
        .akiEfacil .outer_images .img_div {
          flex: 1; } }
      .akiEfacil .outer_images .img_div img {
        max-width: 100%;
        height: auto; }
        @media screen and (min-width: 768px) {
          .akiEfacil .outer_images .img_div img {
            width: 450px; } }
        @media screen and (max-width: 575px) {
          .akiEfacil .outer_images .img_div img {
            width: 160px; } }



