@font-face {
  font-family: 'Glypha-LT';
  src: url("../../../src/styles/fonts/Glypha-LT-Std-55-Roman.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

img {
  max-width: 100%;
  height: auto; }

.services {
  background-color: #ebebeb;
  padding: 50px 0 50px 0; }
  .services .wrapper {
    margin: 0 10% 0 10%; }
    @media screen and (min-width: 768px) {
      .services .wrapper > div:nth-child(3), .services .wrapper > div:nth-child(4), .services .wrapper h1 {
        margin-top: 50px; } }
    .services .wrapper p.title {
      font-family: 'Glypha-LT';
      font-weight: bold;
      font-size: 32px;
      margin-bottom: 50px; }
    .services .wrapper .row {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between; }
      .services .wrapper .row .services_images {
        padding-bottom: 5px; }
  .services img {
    max-width: 100%;
    height: auto; }
  @media screen and (max-width: 767px) {
    .services img.img_services {
      width: 160px; } }
  .services .top_buttons {
    display: flex;
    width: 100%;
    background-color: #ebebeb;
    color: #6da608;
    flex-wrap: wrap; }
    .services .top_buttons h1, .services .top_buttons p {
      font-family: 'Glypha-LT'; }
    @media screen and (min-width: 768px) {
      .services .top_buttons div:nth-child(1) {
        margin-right: 15px; }
      .services .top_buttons div:nth-child(2) {
        margin-left: 15px; } }
    @media screen and (max-width: 767px) {
      .services .top_buttons div:nth-child(1) {
        margin-bottom: 15px; }
      .services .top_buttons div:nth-child(2) {
        margin-top: 15px;
        margin-bottom: 50px; } }
    .services .top_buttons .button {
      background-color: white;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      min-height: 150px; }
      @media screen and (min-width: 768px) {
        .services .top_buttons .button {
          flex: 1; } }
      .services .top_buttons .button p {
        padding-top: 20px;
        margin: 0; }



