@font-face {
  font-family: 'Glypha-LT';
  src: url("../../../src/styles/fonts/Glypha-LT-Std-55-Roman.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

img {
  max-width: 100%;
  height: auto; }

.podeVer {
  display: flex;
  background-color: #ebebeb; }
  @media screen and (max-width: 767px) {
    .podeVer {
      height: 700px; } }
  .podeVer .row {
    display: flex;
    width: 100%;
    align-items: center;
    margin: 0 10% 0 10%; }
    @media screen and (min-width: 768px) {
      .podeVer .row {
        min-height: 500px; } }
  .podeVer .outer_images {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    max-width: 150px;
    text-align: left; }
    .podeVer .outer_images a {
      margin-top: 10px;
      font-size: 15px;
      color: #6da608;
      font-weight: bold; }
  .podeVer .align_images {
    display: flex;
    align-items: end;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap; }
    @media screen and (max-width: 767px) {
      .podeVer .align_images {
        justify-content: space-around; } }
    .podeVer .align_images > div {
      background-color: #ebebeb; }
  .podeVer .inner_images {
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffff; }
    .podeVer .inner_images p {
      font-family: 'Glypha-LT';
      text-align: left;
      font-size: 32px;
      font-weight: bold; }
    .podeVer .inner_images img {
      max-width: 100%;
      height: auto;
      width: 101px;
      height: 100px; }



